<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="" SubModuleCode="CRM_ACCOUNTS" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="" :listType="['List']" :ListData="ContactData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>

<script>
import DataService from "../../../services/DataService";

export default {
    data() {
        return {
            isLoading: false,
            Headers: [{
                "COLUMN_NAME": "first_name",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Name',
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "recruiter_id",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Role Title',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "email_id",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Is Email Sent',
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Is Primary',
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Enable Login',
                "DISPLAY_ORDER": 5
            }],
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0, 
            noRecords:false,          
            searchCondition:'',
            ContactData: [],
            listheaderbuttons: [],
            listActions: []
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('Associate Contact');
            divElement[0].appendChild(spanElement);
        }
    },    
    methods: {
        actionButtonClick: function () {},
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;            
            if (value.searchConditionJson != '') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }           
            this.FetchData();
        },
        commonHeaderButtonClick: function () {},
        renderActions: function () {},
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },      
        FetchData: async function () {     
            var vm = this;
            vm.isLoading = true;
			//var params = `First_Name=&Last_Name=&job_title=&email_id=&business_phone=&mobile_phone=&fax=&preferred_contact=&id=&subModuleCode=CRM_ACCOUNTS&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&statusId=&requestType=&searchCondition${vm.searchCondition}=&AccountType=&clientId=`;
            let params = '';
            if (vm.searchCondition !== '') {
                params = `First_Name=&Last_Name=&job_title=&email_id=&business_phone=&mobile_phone=&fax=&preferred_contact=&id=&subModuleCode=CRM_ACCOUNTS&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&statusId=&requestType=&searchCondition${vm.searchCondition}=&AccountType=&clientId=`;
            } else {
                params = `First_Name=&Last_Name=&job_title=&email_id=&business_phone=&mobile_phone=&fax=&preferred_contact=&id=&subModuleCode=CRM_ACCOUNTS&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&statusId=&requestType=&searchCondition=&AccountType=&clientId=`;
            }
            await DataService.GetClientAssociateContactListing(params).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.isLoading = false;
                    vm.ContactData = JSON.parse(response.data[0][Object.keys(response.data[0])[0]]).data;                
                    vm.TotalRecords = vm.ContactData[0].total_records;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                }else {
                    vm.ContactData=[];
                    vm.noRecords = true;
                    vm.TotalRecords = 0
                    vm.isLoading = false;
                }
            })
        }
    }
}
</script>